"use client";
import React, { useState, useEffect, ReactNode, useCallback } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import {
  YadakchiAdminLayout,
  ListPageLayout,
  Label,
  Badge,
  FormModal, // ایمپورت مودال جدید
  Input,     // ایمپورت برای استفاده در فرم
  Button,    // ایمپورت برای استفاده در فرم
  type ColumnDefinition,
} from "@my-ds/design-system";

import { getWarranties, updateWarranty, deleteWarranty, createWarranty } from "../../services/api/warrantyService";
import { Warranty , PaginatedResponse } from "@/services/api/types";
import { pageSizeOptions } from "../../config/filters.config";

export default function WarrantyPage() {
  const [warrantyData, setWarrantyData] = useState<PaginatedResponse<Warranty> | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [pageSize, setPageSize] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  
  // State برای مدیریت مودال (افزودن/ویرایش)
  const [isFormModalOpen, setIsFormModalOpen] = useState(false);
  const [editingWarranty, setEditingWarranty] = useState<Warranty | null>(null);
  const [formTitle, setFormTitle] = useState('');
  const [actionLoading, setActionLoading] = useState(false);

  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => <Link href={href}>{children}</Link>;

  // تابع واکشی داده‌ها با useCallback برای پایداری
  const fetchWarranties = useCallback(async () => {
    try {
      setIsLoading(true);
      const response = await getWarranties(currentPage, pageSize);
      setWarrantyData(response);
    } catch (err) {
      setError(err instanceof Error ? err.message : "خطا");
    } finally {
      setIsLoading(false);
    }
  }, [currentPage, pageSize]);

  useEffect(() => {
    fetchWarranties();
  }, [fetchWarranties]);

  // --- Handlers ---
  const handleOpenAddModal = () => {
    setEditingWarranty(null);
    setFormTitle('');
    setIsFormModalOpen(true);
  };

  const handleOpenEditModal = (warranty: Warranty) => {
    setEditingWarranty(warranty);
    setFormTitle(warranty.title);
    setIsFormModalOpen(true);
  };

  const handleFormSubmit = async () => {
    setActionLoading(true);
    try {
      if (editingWarranty) {
        await updateWarranty(editingWarranty.id, { title: formTitle });
      } else {
        await createWarranty(formTitle);
      }
      setIsFormModalOpen(false);
      await fetchWarranties(); // رفرش کردن لیست
    } catch (err) {
      alert("خطا در ذخیره‌سازی گارانتی.");
    } finally {
      setActionLoading(false);
    }
  };

  const handleDelete = async (warrantyId: number) => {
    if (window.confirm("آیا از حذف این گارانتی مطمئن هستید؟")) {
      await deleteWarranty(warrantyId);
      await fetchWarranties();
    }
  };

  // --- Column Definitions ---
  const warrantyColumns: ColumnDefinition<Warranty>[] = [
    { accessorKey: 'id', header: 'شناسه' },
    { accessorKey: 'title', header: 'عنوان گارانتی', cell: (w) => <Label text={w.title} size="md" /> },
    { accessorKey: 'isActive', header: 'وضعیت', cell: (w) => <Badge label={w.isActive ? "فعال" : "غیرفعال"} variant={w.isActive ? "success" : "neutral"} /> },
    { accessorKey: 'createDate', header: 'تاریخ ثبت', cell: (w) => <Label text={new Date(w.createDate).toLocaleDateString('fa-IR')} size="md" /> },
  ];
  
  // --- Props Preparation ---
  const listPageProps = {
    listPageHeaderProps: {
      title: "مدیریت گارانتی‌ها",
      totalItems: warrantyData?.totalCount,
      actions: <Button onClick={handleOpenAddModal}>افزودن گارانتی جدید</Button> 
    },
    tableProps: {
      columns: warrantyColumns,
      data: warrantyData?.items || [],
      emptyStateMessage: "هیچ گارانتی یافت نشد.",
      rowActions: (warranty: Warranty) => ({
        onEdit: () => handleOpenEditModal(warranty),
        onDelete: () => handleDelete(warranty.id),
      }),
    },
    paginationProps: {
      currentPage: currentPage,
      pageSize: pageSize,
      totalCount: warrantyData?.totalCount || 0,
      onPageChange: (page: React.SetStateAction<number>) => setCurrentPage(page),
      pageSizeOptions: pageSizeOptions,
      onPageSizeChange: (size: React.SetStateAction<number>) => { setPageSize(size); setCurrentPage(1); },
    },
    isLoading: isLoading,
    error: error,
  };

  // --- JSX ---
  return (
    <>
      <YadakchiAdminLayout
        currentPath={pathname}
        renderLink={renderLink}
      >
        <ListPageLayout {...listPageProps} />
      </YadakchiAdminLayout>

      {/* مودال فرم را در انتهای JSX قرار می‌دهیم */}
      <FormModal
        isOpen={isFormModalOpen}
        onClose={() => setIsFormModalOpen(false)}
        title={editingWarranty ? "ویرایش گارانتی" : "افزودن گارانتی جدید"}
        onConfirm={handleFormSubmit}
        isLoading={actionLoading}
        confirmText={editingWarranty ? "ذخیره تغییرات" : "ایجاد گارانتی"}
      >
        <form onSubmit={(e) => { e.preventDefault(); handleFormSubmit(); }}>
          <Input
            id="warranty-title"
            label="عنوان گارانتی"
            value={formTitle}
            onChange={(e) => setFormTitle(e.target.value)}
            required
            autoFocus
          />
        </form>
      </FormModal>
    </>
  );
}